.navbar-expand-lg {
  position: fixed;
  width: 100%;
}

.navbar-brand {
  font-weight: bold;
  font-family: monospace;
  font-size: 28px;
}

.navbar-nav > .nav-link > a {
  color: red;
}

.navbar-dark .navbar-nav .nav-link {
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
}

.navbar-dark .navbar-nav .nav-item {
  margin: 0 15px;
}

body {
  margin: 0;
  padding: 0;
}
.homepageheader {
  height: 100vh;
  width: auto;
  background-image: url("./tree.jpg");
}
.homepagecontent {
  color: black;
  padding-top: 36.5rem;
  padding-left: 5rem;
  text-align: left;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}
.homepagenavigation {
  background-color: white;
  color: black;
  padding: 0.5rem 0;
  position: fixed;
  width: 100%;
  top: 0%;
  box-shadow: 0px 0px 4px black;
}
.homepagenavcontent {
  color: black;
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  align-items: center;
}

